.footer{
	width: 100%;
	height: 541px;
	flex: 0 0 auto;

	background-color: $pink;
	color: #fff;
}
.footer_container{
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	max-width: 1440px;
	margin: 0 auto;
	height: 100%;
	padding: 80px 80px 0;

	.footer_content{
		display: flex;

		.footer_info{
			width: 300px;
			margin-right: 32px;

			.footer_info_text{
				line-height: 21px;
			}
		}

		.footer__blocks{
			width: calc(100% - 332px);
			display: flex;
			justify-content: space-between;

			.footer__block{
				display: flex;
				flex-direction: column;

				.footer_form{
					width: 296px;

					.footer_input{
						height: 48px;
						margin-bottom: 16px;

						border: none;
						border-radius: 24px;

						color: #A8AAB7;
						font-size: 16px;
					}
					.footer_button{
						height: 48px;
						margin-bottom: 16px;

						border: none;
						border-radius: 24px;
						background-color: $yellow;

						color: $darkPink;
						font-size: 16px;
						font-weight: 600;
						text-transform: uppercase;
						cursor: pointer;

						transition: all 0.2s ease-in-out;
					}
					.footer_button:hover{
						background-color: #FFDCA7;

					}
				}
				.footer_auth{
					display: flex;
					gap: 24px;
				}
			}
		}
	}
	.footer_bottom{
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		max-width: 1282px;
		height: 97px;

		.divider{
			position: absolute;
			left: 0;
			bottom: 97px;

			width: 100vw;
			height: 1px;

			background-color: #F6ADAD;
		}
		.pay{
			display: flex;
			gap: 8px;
		}
		.copyright{
			position: absolute;
			bottom: 42px;
			left: calc(50% - 75px);
		}
		.socials{
			display: flex;
			gap: 24px;
		}
	}
}
.footer_title{
	font-weight: 600;
	font-size: 20px;
	line-height: 30px;
	margin-bottom: 16px;
}
.footer__block_text{
	color:#fff;
	line-height: 21px;
	margin-bottom: 8px;
}
.footer__block_pd{
	padding-right: 50px;
}
