.meet{
	height: 1189px;
	background: #105393;
	position: relative;
	text-align: center;
	align-content: center;
	.stripes{
		position: absolute;
		top: 190px;
		left: 435px;
	}
	button{
		width: 180px;
		height: 56px;
		background: #FF8F8F;
		border: none;
		font-size: 16px;
		font-weight: 600;
		color: #ffffff;
		text-transform: uppercase;
		border-radius: 38px;
		margin-top: 40px;
	}
	.p-40{
		padding: 0px 100px;
	}
	p{
		font-size: 20px;
		font-weight: 400;
		text-align: center;
		color: #FFE4BB;
		padding-bottom: 24px;
	}
	.sld{
		width: 85%;
		justify-content: space-between;
		margin-left: auto;
		margin-right: auto;
	}
	h1{
		color: #FFE4BB;
		text-align: center;
		padding-top: 90px;
		padding-bottom: 8px;
	}
	.pgn{
		display: flex;
		justify-content: space-around;
		font-size: 20px;
		font-weight: 600;
		color: #6F92B4;
		width: 60%;
		margin-left: auto;
		margin-right: auto;
		padding-top: 120px;
		.activity{
			color: #FFE4BB;
			text-decoration: underline;
			text-underline-offset: 1rem;
		}
	}
	.meet_new{
		position: relative;
	}
	.meet_item_photo{
		width: 80%;
		height: 612px;
		object-fit: cover;
	}
	.meet_content{
		position: absolute;
		display: flex;
		flex-direction: column;
		text-align: center;
		width: 1000px;
		z-index: 10;
		top: 50%;
		left:50%;
		transform:translate(-50%, -50%);

		.meet_title{
			position: relative;
			.stripes{
				position: absolute;
				left: -5px;
				top: -35px;
			}
			.star{
				position: absolute;
				right: 80px;
				top: -80px;
			}
			.rainbow{
				position: absolute;
				right: -70px;
				top: 170px;
			}
		}
		.meet_buttons{
			display: flex;
			justify-content: center;
			gap: 32px;
		}
	}

	.meet_slider {
		.slick-arrow {
			position: absolute;
			top: calc(50% - 35px);
			font-size: 0;
			border: 0;
			z-index: 10;
		}
		.meet_prev{
			height: 22px;
			width: 13px;
			left: 45px;
		}
		.meet_next{
			height: 22px;
			width: 13px;
			right: 45px;
		}
		.slick-dots{
			content: 'first';
			position: absolute;
			z-index: 10;
			left:50%;
			bottom: 40px;
		}
		.slick-dots li button{
			font-size: 20px;
			background: $colorWhite;
			opacity: 0.7;
		}
	}
}
